         $("#manualStockCheckId").DataTable().destroy();
        if ( this.manualStockCheckDataList == null ) {
            this.manualStockCheckDataList = [];
        }
      
        
        if ( this.manualStockCheckDataList.length == 0 ) {
            this.manualStockCheckDataList.push( this.newAttribute );
        } else {
            this.manualStockCheckDataList.push( this.newAttribute );
        }
